<template>
<div id="box">
<ul id="con1" ref="con1" :class="{anim:animate==true}">
<li v-for="item in items" :key="item.id" @mouseenter="stopscoll" @mouseleave="startscoll" >
    <div style="width:200px;height:150px;overflow:hidden;text-align:left;">
             {{item.name}}
    </div>
  
    
    </li>
</ul>
</div>
</template>
<script>
export default {
data() {
return {
timer:null,
animate:false,
items:[
{name:"中华人民共和国第一条"},
          {name:"警察手册"},
          {name:"法律法规注意事项"},
          {name:"中华人民共和国第四条"},
          {name:"劳动法00"},
          {name:"中华人民共和国第六条"},
          {name:"可持续型反转"},
]
}
},
created(){
clearInterval(this.timer)
this.timer = null
this.setTimer()
},
destroyed: function () {
// 每次离开当前界面时，清除定时器
clearInterval(this.timer)
this.timer = null
},
methods: {
stopscoll(){
clearInterval(this.timer)
this.timer=null
},
startscoll(){
clearInterval(this.timer)
this.timer = null
this.setTimer()
},
setTimer(){
var that = this;
if(that.timer==null){
that.timer=setInterval(this.scroll,2000)
}
}, 
scroll(){
this.animate=true; // 因为在消息向上滚动的时候需要添加css3过渡动画，所以这里需要设置true
setTimeout(()=>{ // 这里直接使用了es6的箭头函数，省去了处理this指向偏移问题，代码也比之前简化了很多
this.items.push(this.items[0]); // 将数组的第一个元素添加到数组的
this.items.shift(); //删除数组的第一个元素
this.animate=false; // margin-top 为0 的时候取消过渡动画，实现无缝滚动
},500)
}
}
}
</script>
<style scoped>
*{
margin: 0 ;
padding: 0;
}
#box{
width: 300px;
height:300px;
overflow: hidden;
padding-left: 30px;
}
.anim{
transition: all 0.5s;
margin-top: -150px;
}
#con1 li{
list-style: none;
/* line-height: 30px; */
height: 150px;
cursor: pointer;
color: #fff;

}
</style>



