<template>
    <div   id="detail">
         <el-row :gutter="10">
         <el-col :xs="24" :sm="24" :md="24" ><div class="grid-content top">
           <span>信息列表</span>    
        </div></el-col>
       </el-row>
       <el-table
      
       ref="singleTable"
        @current-change="handleCurrentChange"
       highlight-current-row
      :data="tableData"
      style="width: 90%;margin-top:20px;margin-left:5%;border-top: 1px solid #ccc;" size="small">
      <el-table-column
        prop="workerCode"
        label="员工编号"
        width="500">
      </el-table-column>
      <el-table-column
        prop="name"
        label="员工姓名"
       width="500">
      </el-table-column>
      <el-table-column
        prop="sex"
        label="性别"
        width="500">
      </el-table-column>
     
     
    </el-table>
    </div>
</template>
<script>
export default {
    methods: {
        
    },
}
</script>
<style >
     #detail .top{
   border-bottom: 1px solid #ccc;
   text-align: left;
   font-size: 24px;
   margin-bottom: 15px;
  }
    #detail div.cell{
    text-align: center
}
</style>


