<template>
    <div class="carinfo">
        <el-row :gutter="10">
        <el-col :xs="24" :sm="24" :md="24" ><div class="grid-content top">
           <span>车辆基本信息登记</span>    
        </div></el-col>
       </el-row>
        <form >
       <div style="margin-left:30px;">
           <el-row :gutter="10">
                <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td><span style="color:red">*</span>登记时间：</td>
                   <td>
                    <div style="width:300px;height:32px;border:1px solid #999;background:#FFF;line-height:32px;" id="time"> 
                                <el-date-picker
                                   v-model="time"
                                   type="date"
                                    value-format="yyyyMMdd"
                                   placeholder="选择日期"  size="mini" style="width:150px;"  @blur="receive">
                               </el-date-picker>
                        </div>    
                    </td>     
                 </tr>     
                </table>    
                </div></el-col>
                <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td><span style="color:red">*</span>登记序号：</td>
                   <td><select  id="" name="receiveId"><option value="" >默认</option>
                       <option :value="item" v-for="item of list" :key="item.id">{{item}}</option></select></td>      
                 </tr>     
                </table>    
                </div></el-col>
                <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td><span style="color:red">*</span>车辆类型：</td>
                   <td><input type="text" placeholder="请输入" name="carType"></td>     
                 </tr>     
                </table>    
                </div></el-col>
                <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td><span style="color:red">*</span>车主：</td>
                   <td><input type="text" placeholder="请输入" name="owner"></td>     
                 </tr>     
                </table>    
                </div></el-col>
                <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td><span style="color:red">*</span>车辆品牌：</td>
                   <td><input type="text" placeholder="请输入" name="brand"></td>     
                 </tr>     
                </table>    
                </div></el-col>
                <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td><span style="color:red">*</span>车辆颜色：</td>
                   <td><input type="text" placeholder="请输入" name="color"></td>     
                 </tr>     
                </table>    
                </div></el-col>
                <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td><span style="color:red">*</span>车架号：</td>
                   <td><input type="text" placeholder="请输入" name="frameNumber"></td>     
                 </tr>     
                </table>    
                </div></el-col>
                <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td><span style="color:red">*</span>发动机号：</td>
                   <td><input type="text" placeholder="请输入" name="engineNumber"></td>     
                 </tr>     
                </table>    
                </div></el-col>
                <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td><span style="color:red">*</span>车牌号：</td>
                   <td><input type="text" placeholder="请输入" name="plateNumber"></td>     
                 </tr>     
                </table>    
                </div></el-col>
                <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td><span style="color:red">*</span>车辆图片：</td>
                   <td>
                     <el-upload
                     action="123"
                    :multiple="multiple"
                    :auto-upload="false"
                    list-type="picture-card"
                    :on-preview="handlePictureCardPreview"
                    :on-remove="handleRemove"
                    :limit="3"
                    ref="upload"
                    :http-request="uploadFile"
                     :on-change="handleChange"
                    >
                    <i class="el-icon-plus"></i>
                    </el-upload>
                    <el-dialog :visible.sync="dialogVisible">
                    <img width="100%" :src="dialogImageUrl" alt="">
                    </el-dialog>
                   </td>     
                 </tr>     
                </table>    
                </div></el-col>
                </el-row>
         </div>
          <div >
           <el-row>
                <el-button type="primary" @click="confirm()">确定</el-button>
                <el-button type="info">取消</el-button>
            </el-row>
         </div>
       </form>
    </div>
</template>
<script>
import axios from "axios"
let formData = new FormData();
export default {
    data() {
        return {
          plateNumber:"",
          engineNumber:"",
          frameNumber:"",
          color:"",
          brand:"",
          owner:"",
          carType:"",
          receiveId:"",
          list:"",
          dialogImageUrl: '',
          dialogVisible: false,
           multiple:true,
           formData:"",
           fileList: [],
           time:""
            
        }
    },
    methods: {
       uploadFile(file){
                 this.formData.append("file", file.file);
            },
       handleRemove(file, fileList) {
       console.log(fileList)
      },
       handleChange(file, fileList) {
        this.fileList = fileList;
    },
      handlePictureCardPreview(file) {
        this.dialogImageUrl = file.url;
        this.dialogVisible = true;
      },
      confirm(){
        let formData = new FormData();
          formData.append("engineNumber", this.engineNumber);
          formData.append("frameNumber", this.frameNumber);
          formData.append("color", this.color);
          formData.append("brand", this.brand);
          formData.append("owner", this.owner);
          formData.append("carType", this.carType);
          formData.append("receiveId", this.receiveId);
          for(let i=0;i<this.fileList.length;i++){
            formData.append("file", this.fileList[i].raw);
          }
         
         
          axios({
        url: process.env.API_HOST+'/qy/RegistrationCarInfo',
        method: "post",
        data: formData,
        headers: {
          "Content-Type": "multipart/form-data;charset=utf-8",
          token:JSON.parse(localStorage.getItem('data')).token
        }
      })
        .then((res) => {
           let that=this
         console.log(res)
          switch(res.data.code){
                           case 200:
                            that.$message({
                                 message:'提交成功',
                                  type: 'success'
                            })
                            break;
                            case 404:
                                 that.$message({
                                 message:'提交失败',
                                  type: 'error'
                            })
                            break;
                            case 505:
                                 that.$message({
                                 message:'token失效重新登录',
                                  type: 'warning'
                            })
                            that.$router.push("/login")
                            break;

                      }
        })
        .catch(err => {
          console.log(err);
        });
},
         receive(){
                      axios({
                     method: 'post',
                     headers: {
                         'Content-Type':'application/json;charset=UTF-8',
                          token:JSON.parse(localStorage.getItem('data')).token
                            },
                           url: process.env.API_HOST+'/qy/getReceiveId', 
                           data:JSON.stringify({
                            startTime:this.time,
                            })
                   }).then((res)=>{
                       console.log(res)
                     switch(res.data.code){
                       case 200:
                       this.list=res.data.data
                       break;
                       case 505:
                         this.$router.push("/login")
                         break;
                       default:
                         this.$message({
                           message:"错误",
                           type:"error"
                         })  
                     }
             })
         }
    },
}
</script>
<style lang="css" >
  .carinfo  .top{
   border-bottom: 1px solid #ccc;
   text-align: left;
   padding: 0 0 20px;
   font-size: 24px;
  }
  .carinfo .grid-content{
      margin: 10px 0;
      margin-left: 30px;
  }
  .carinfo table tr td:nth-child(1){
     width: 140px;
     /* border: 1px solid red; */
     text-align: left;
  }
  .carinfo select{
      width:304px;
      height: 32px;
  }
  .carinfo input{
      width: 300px;
      height: 28px;
  }
 .carinfo .el-row{
     text-align: left;
     margin-left: 10%;
     margin-top: 20px;
 }
 .carinfo table tr td #time input.el-input__inner{
    border:0  !important;
    width: 200px;
} 
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>


