<script>
 import axios from "axios"
export default {
    data() {
        return {
            time:'',
              openDate: "",
        companyType: "",
        register: '',
        cname: "",
        caddress: "",
        contway: '',
        fax: "",
        email: "",
        postcode: "",
        ecotype: "",
        legPersonCode: "",
        legPerson: '',
        legPersonIdcode: '',
        defPrincipal: "",
        defDepartContway: "",
        businessScope: "",
        registeredCapital: "",
        floorSpace: '',
        agency: '',
        agencyName: '',
        specRecordCode: '',
        specRecordAgency: "",
        specRecordAname: '',
        induLicenceCode: '',
        induLicenceAgency: '',
        unisocialCreditCode: '',
        unisocialLicenceAgency: '',
        busLicenceCode: "",
        busLicenceAgency: '',
        taxLicenceCode: '',
        taxLicenceAgency: '',
        ecotypelist: '',
        registerlist:'',
        p:false,
        f:false,
        c:false

        }
    },
    mounted() {
        this. getEcotype(),
        this.getRegister()
    },
    methods: {
        define(){
            axios({
          method: 'post',
          url: process.env.API_HOST+'/qy/workReg',
          headers: {
            'Content-Type': 'application/json;charset=UTF-8'
          },
          data: JSON.stringify({
            openDate: this.time,
            companyType: this.companyType,
            register: this.register,
            cname: this.cname,
            caddress: this.caddress,
            contway: this.contway,
            fax: this.fax,
            email: this.email,
            postcode: this.postcode,
            ecotype: this.ecotype,
            legPersonCode: this.legPersonCode,
            legPerson: this.legPerson,
            legPersonIdcode: this.legPersonIdcode,
            defPrincipal: this.defPrincipal,
            defDepartContway: this.defDepartContway,
            businessScope: this.businessScope,
            registeredCapital: this.registeredCapital,
            floorSpace: this.floorSpace,
            agency: this.agency,
            agencyName: this.agencyName,
            specRecordCode: this.specRecordCode,
            specRecordAgency: this.specRecordAgency,
            specRecordAname: this.specRecordAname,
            induLicenceCode: this.induLicenceCode,
            induLicenceAgency: this.induLicenceAgency,
            unisocialCreditCode: this.unisocialCreditCode,
            unisocialLicenceAgency: this.unisocialLicenceAgency,
            busLicenceCode: this.busLicenceCode,
            busLicenceAgency: this.busLicenceAgency,
            taxLicenceCode: this.taxLicenceCode,
            taxLicenceAgency: this.taxLicenceAgency
          })
        }).then((res) => {

          if (res.data.code == 200) {
            this.$message({
              message: '注册成功',
              type: 'success'
            });
            let that = this
            setTimeout(function () {
              that.$router.push("/login")
            }, 100)


          } else {
            this.$message({
              message: '注册失败',
              type: 'error'
            });
          }


        }).catch((err) => {
          this.$message({
            message: '注册失败',
            type: 'error'
          });
        })
        },
        reset(){
            this.time="",
            this.openDate = "",
           this.companyType = "",
           this.register = '',
           this.cname = "",
           this.caddress = "",
           this.contway = '',
           this.fax = "",
           this.email = "",
           this.postcode = "",
           this.ecotype = "",
           this.legPersonCode = "",
           this.legPerson = '',
           this.legPersonIdcode = '',
           this.defPrincipal = "",
           this.defDepartContway = "",
           this.businessScope = "",
           this.registeredCapital = "",
           this.floorSpace = '',
           this.agency = '',
           this.agencyName = '',
           this.specRecordCode = '',
           this.specRecordAgency = "",
           this.specRecordAname = '',
           this.induLicenceCode = '',
           this.induLicenceAgency = '',
           this.unisocialCreditCode = '',
           this.unisocialLicenceAgency = '',
           this.busLicenceCode = "",
           this.busLicenceAgency = '',
           this.taxLicenceCode = '',
           this.taxLicenceAgency = ''
        },
        phone(){
             if(!this.contway.match(/^1((3[\d])|(4[5,6,9])|(5[0-3,5-9])|(6[5-7])|(7[0-8])|(8[1-3,5-8])|(9[1,8,9]))\d{8}$/)){
                  this.p=true
             }else{
               this.p=false  
             }
           
        },
        idcard(){
             if(!this.legPersonIdcode.match(/(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/
)){
                  this.c=true
             }else{
               this.c=false  
             }
           
        },
        cfox(){
             if(!this.fox.match(/^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/)){
                  this.f=true
             }else{
               this.f=false  
             }
           
        },
       
        login(){
            this.$router.push('/login')
        },
       getRegister(){
              axios({
          method: 'post',
          url: process.env.API_HOST+ '/qy/getRegister',
          headers: {
            'Content-Type': 'application/json;charset=UTF-8'
          },
        }).then(res=>{
            console.log(res)
                this.registerlist=res.data.data;
        })
          
        },
          getEcotype() {
           axios({
          method: 'post',
          url: process.env.API_HOST+ '/qy/getEcotype',
          headers: {
            'Content-Type': 'application/json;charset=UTF-8'
          },
        }).then((res) => {
          this.ecotypelist = res.data.data;
        })
      }
        
    },
}
</script>
<style >
   .register{
  position: fixed;
  width: 100%;
  height: 100%;
  background: #F3F5FE;
     overflow: scroll;
   
  }
  .register  .contain{
      width: 95%;
      height: 120%;
      border :1px solid #ccc;
      margin: 20px auto;
      border-radius:5px;
      background: #fff; 
      margin-top:100px;
   
      }
     .register table tr td #time input.el-input__inner{
    border:0  !important;
    width: 200px;
}   
</style>


